
import Spinner from 'vux/src/components/spinner/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XImg from 'vux/src/components/x-img/index.vue'
import Card from 'vux/src/components/card/index.vue'
import FormPreview from 'vux/src/components/form-preview/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import api from '../api/api'
import env from '../config/env'
export default {
    data() {
        return {
            id: -1,
            product: {},
            productItem: [],
            title: '',
            showLoading: false,
            loadingType: 'lines',
        }
    },
    mounted() {
        this.id = this.$route.params.id
        this.initData()
    },
    methods: {
        async initData() {
            let vm = this
            // 显示加载动画
            vm.showLoading = true
            // 根据产品id获取产品详细信息
            api.get('getProductById/' + vm.id).then((res) => {
                if(res.status===1){
                    // 产品对象
                    vm.product = res.data
                    vm.title = vm.product.type === '0' ? '课程' : '商品'
                    vm.productItem.push({label: "品牌：", value: vm.product.brand})
                    vm.productItem.push({label: "原价：", 
                        value: "￥"+vm.product.original_price.toString()})
                    vm.productItem.push({label: "会员价：", 
                        value: "￥"+vm.product.member_price.toString()})
                    if(vm.title === '商品'){
                        vm.productItem.push({label: "现货数量：", value: vm.product.quantity.toString()})
                    }
                    vm.productItem.push({label: "简介：", value: vm.product.description})
                }else{
                    vm.$vux.alert.show({
                        title: env.alertInfo,
                        content: res.message,
                        buttonText: '确定',
                        dialogTransition:'',
                        maskTransition:''
                    })
                }
                // 隐藏加载动画
                vm.showLoading = false
            }).catch(error => {
                vm.$vux.alert.show({
                    title: env.alertInfo,
                    content: env.errorInfo,
                    buttonText: '确定',
                    dialogTransition:'',
                    maskTransition:''
                })
            })
        },
        getImgUrl(img_url) {
            return img_url && env.serverUrl + img_url
        },
        // 立即下单
        order() {
            // 跳转至订单确认页面
            this.$router.push({path: '/order/'+this.product.id});
        },
        back() {
            this.$router.replace({path: '/shop'});
        }
    },
    components: {
        Spinner,
        XHeader,
        XImg,
        Card,
        FormPreview,
        XButton
    }
}



